<template>
    <div class="header">
       <router-link tag="div" to="/" class="input">
        <span class="icon-bg"></span>
        <span class="input-title">121活动套餐大优惠</span>
       </router-link>
        <div class="icon-right"><van-icon name="wap-nav" /></div>
    </div>
</template>

<script>
import { Icon } from 'vant';
import 'vant/lib/icon/local.css';
export default {
    name:'homeHader',
    components:{
        [Icon.name]:Icon
    }
} 
</script> 
<style lang="less" scoped>
@import '~styles/theme.less';
.header{
    display:flex;
    line-height:@headerHeight;
    height: @headerHeight;
    background:@bgColor;
    color:#fff;
    padding: 0 .24rem ;
    align-items: center;
    justify-content: space-between;
    z-index:99;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}
.icon-right{
    display: flex;
    height: 1rem;
    padding-left: .2rem;
    font-size: .45rem;
    align-items: center;
}
.input{
    width: 90%;
    height:.60rem;
    line-height:.60rem;
    flex :1;
    overflow: hidden;
    background-color: #fff;
    border-radius: 4px;
    font-size: .25rem;
}
.input-title{
    color: #808080;
}
.icon-bg{
    padding-left: .2rem;
    font-size: .32rem;
    color: #808080;
}
</style>




